<script setup lang="ts">
import useAsync from "~/composables/use-async";
import usePagination from "~/composables/use-pagination";
import { COMMERCE_TRANSACTION_STATUS, COMMERCE_TRANSACTION_TYPE, COMMERCE_TYPE, SORT, THEME_COLOR } from "~/constants";
import BillingAPI from "~/services/billing";
import getErrorLocalePathMessage from "~/utils/locale";

const { t, te } = useI18n();
const color = useColorMode();
const toaster = useToast();

const { data: creditCard, loading: loadingCreditCard, error: errorCreditCard, run: loadCreditCard } = useAsync(BillingAPI.load);

const onSuccess = () => {
  toaster.success(t("pages.account_billing.alert_credit_card_delete_success"));
  loadCreditCard();
};

const onError = (error: Error) => {
  const { message } = getErrorLocalePathMessage(error.message, { t, te });
  return toaster.error(message);
};

const { loading: deletingCard, run: deleteCreditCard } = useAsync(BillingAPI.delete, { onSuccess, onError });

const {
  items: billingOrders,
  loading: loadingBillingHistory,
  error: errorBillingOrders,
  canLoadMore,
  loadMore,
} = usePagination<IModelBillingOrder>({
  immediate: true,
  pageSize: 5,
  loadItemsFn: (page: number, size: number) =>
    BillingAPI.loadHistory({
      size,
      page,
      sort: SORT.added,
      next: true,
      status: COMMERCE_TRANSACTION_STATUS.APPROVED,
      type: [COMMERCE_TRANSACTION_TYPE.CREDIT_CARD_PAY, COMMERCE_TRANSACTION_TYPE.REDEEM_ACCESS_CODE],
    }),
});

const errorMessage = computed(() => (errorBillingOrders.value && getErrorLocalePathMessage(errorBillingOrders.value, { t, te }).message)
  || (errorCreditCard.value && getErrorLocalePathMessage(errorCreditCard.value, { t, te }).message),
);

onMounted(() => loadCreditCard());
definePageMeta({ layout: "home", middleware: "auth" });
</script>

<template>
  <div>
    <BaseModal root-class="max-w-[550px]">
      <template #trigger="{ openModal }">
        <div v-if="loadingCreditCard" class="mb-10">
          <CardCreditSkeleton />
        </div>
        <div v-else-if="creditCard" class="mb-10">
          <CardCredit :card="creditCard" has-delete @delete="openModal" />
        </div>
        <BaseAlert v-if="errorMessage" type="danger" class="my-6" :text="errorMessage" />
        <div class="text-heading-3 text-main mb-6">
          {{ t('pages.account_billing.payment_history_title') }}
        </div>
        <div>
          <BaseTable
            class="text-main"
            :data="billingOrders"
            :loading-text="t('labels.loading')"
            :empty-text="t('pages.account_billing.payment_history_empty_text')"
          >
            <BaseTableCol :heading="t('labels.date')" field="date" _class="text-xs md:text-sm lg:text-base text-secondary rtl:pl-[1rem] ltr:pr-[1rem]" />
            <BaseTableCol :heading="t('labels.type')">
              <template #body="item">
                <div class="text-secondary text-xs md:text-sm lg:text-base rtl:pl-[1rem] ltr:pr-[1rem] min-w-[3rem]">
                  {{ item.data.row.type === COMMERCE_TYPE.PPV ? t('labels.ppv_tickets', 0) : t('labels.commerce_category_labels.subscription') }}
                </div>
              </template>
            </BaseTableCol>
            <BaseTableCol :heading="t('labels.name')" field="name">
              <template #body="item">
                <div class="text-xs md:text-sm lg:text-base rtl:pl-[1rem] ltr:pr-[1rem] break-word line-clamp-3">
                  {{ item.data.row.name }}
                </div>
              </template>
            </BaseTableCol>
            <BaseTableCol :heading="t('labels.amount')" align="right" field="amount" _class="text-xs md:text-sm lg:text-base uppercase whitespace-nowrap" />
          </BaseTable>
        </div>
        <div v-if="canLoadMore" class="mt-5 flex justify-center">
          <BaseButton :disabled="loadingBillingHistory" name="load-more" link type="primary" class="text-heading-4 mx-8 truncate font-medium" @click="loadMore">
            {{ t('labels.load_more') }}
            <IconChevronDown class="inline " :height="8" :width="8" />
          </BaseButton>
        </div>
      </template>
      <template #before-title>
        <div class="mb-6 flex flex-col items-center justify-center">
          <IconCreditCardDelete :is-dark-theme="color.value === THEME_COLOR.DARK" />
          <div class="mt-4 text-xl text-main font-semibold text-center" v-html="t('pages.account_billing.label_modal_confirm_credit_card_delete')" />
        </div>
      </template>
      <template #actions="{ closeModal }">
        <div class="mt-6 grid grid-cols-2 gap-4">
          <BaseButton
            full-width
            class="text-button-bold text-white"
            type="secondary"
            name="modalClose"
            :disabled="deletingCard"
            @click="closeModal()"
          >
            {{ t('button.no') }}
          </BaseButton>
          <BaseButton
            full-width
            type="primary"
            name="modalConfirm"
            class="text-button-bold"
            :disabled="deletingCard"
            @click="deleteCreditCard().then(closeModal)"
          >
            {{ t('button.delete') }}
          </BaseButton>
        </div>
      </template>
    </BaseModal>
  </div>
</template>
