<script setup lang="ts">
import type { ColProps } from "./TableCol.vue";
import { type Slots, useSlots } from "vue";

export interface BaseTableProps {
  data?: Record<string, any>[];
  loading?: boolean;
  disabled?: boolean;
  emptyText?: string;
  loadingText?: string;
}

const props = withDefaults(defineProps<BaseTableProps>(), {
  loadingText: "Loading...",
  emptyText: "No items to display",
});
const { locale } = useI18n();
// TODO: Think about reactivity since slots will not be rerendered on change currently.
const slots: Slots = useSlots();

const filterCols = (slot: any) => {
  return slot.type && "heading" in (slot.props || {});
};

const formatCell = (slot: any) => ({
  ...(slot.props as ColProps),
  slotBody: slot.children?.body,
});

const cells = computed(() => (slots.default ? slots.default().filter(filterCols).map(formatCell) : []));
const cellAlign = computed(() => locale.value === "he" ? "right" : "left");
</script>

<template>
  <table class="baseTable" role="table" :class="{ baseTable_loading: props.loading, baseTable_disabled: props.disabled }">
    <thead v-if="cells.length" role="rowgroup">
      <tr role="row" class="text-secondary">
        <th
          v-for="(cell, index) in cells"
          :key="`table-head-heading-${index}`"
          :align="cell.align || cellAlign"
          role="cell"
          class="text-caption-medium"
        >
          {{ cell.heading }}
        </th>
      </tr>
    </thead>
    <tbody role="rowgroup">
      <tr v-if="props.loading">
        <td :colspan="cells.length">
          <slot name="loading">
            <p class="text-center text-secondary">
              {{ props.loadingText }}
            </p>
          </slot>
        </td>
      </tr>

      <template v-else-if="props.data && props.data.length">
        <tr
          v-for="(row, rowIndex) in props.data"
          :key="`table-body-row-${rowIndex}`"
          role="row"
          tabindex="0"
          class="text-body-medium text-main"
        >
          <td
            v-for="(cell, cellIndex) in cells"
            :key="`table-body-cell-${cellIndex}`"
            :class="cell._class"
            role="cell"
            :align="cell.align || cellAlign"
          >
            <component :is="cell.slotBody" v-if="cell.slotBody" :data="{ row, cell }" />
            <template v-else>
              {{ (cell.field && row[cell.field]) || '---' }}
            </template>
          </td>
        </tr>
      </template>

      <tr v-else>
        <td :colspan="cells.length">
          <slot name="empty">
            <p class="text-center text-secondary">
              {{ props.emptyText }}
            </p>
          </slot>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<style lang="scss">
.baseTable {
  @apply min-w-full;
  border-collapse: collapse;

  tr {
    border-top: 1px solid var(--base-header-line-color);
    border-bottom: 1px solid var(--base-header-line-color);
    td,
    th {
      @apply py-4;
    }
  }

  &.baseTable_loading {
    @apply cursor-wait opacity-75;
  }

  &.baseTable_disabled {
    @apply cursor-not-allowed opacity-[55];
  }
}
</style>
